.container {
    max-width: 1200px;
    padding: 12px;
    margin: 0 auto;
    width: 100%;
}
.booking-form{
    padding:30px;
    border-radius:20px;
    background-color:#ffffff;
    box-shadow : 0 0 40px 5px rgb(0 0 0 / 5%);
}
.form-title{
    font-size:30px;
    margin-bottom:10px;
}
.form-row{
    display:flex;
    flex-wrap:wrap;
}
.col-lg-3{
    flex: 0 0 auto;
    width:25%;   
    position: relative;
}
.form-tab{
    margin-top: 10px;
}
.form-control{
    padding: 14px 50px 14px 20px;
    font-size: 14px;
    box-shadow: none;
    color: #000;
    display: block;
    width: 100%;
    font-weight: 400;
    line-height: 1;
    color: #212529;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 0.375rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

select#cab_type {
    width: 100%;
    height: 48px;
    padding: 14px 50px 14px 20px;
    line-height: 45px;
    border-radius: 10px;
    font-size: 16px;
    font-size: 14px;
    border-color: #ced4da;
    color: #545454;
}
.pos-btn{
    position: absolute;
    bottom: 11%;
    left: 6%;
}
.booking_section{position: relative;
    margin-top: -70px;

                    }